<script lang="coffeescript">

  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()
  import Indent from "braintapper-svelte-indent"
  import {
    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { 
    debug,
    ButtonIcon,
    FormatEllipsis
    IconSvg,
    IconToggleSvg,
    InlineInput 
  } from "../../Braintapper"


  import { 
    ProgressPopover,  
    DueDatePopover, 
    ListItemRow
    ListItemMenu,
    ListHoverableContainer
    
    } from "../../Application"


  




  
  export list = undefined
  export resource = undefined # radarLIst
  



  goToList = ()->
    location.href="#/lists/#{listId}"

  

  updateListItem = (e)->
    console.log "Radar._ListItems menu"
    console.log e.detail

    if Object.has(e.detail, "is_expanded")
      # repaint
      found = list.list_items.findIndex (o)->
        o._id == e.detail._id
      console.log found
      if found?
        list.list_items[found].is_expanded = e.detail.is_expanded
        # list.sort()
        console.log "need to resort"
        list.list_items = resource.sorted(list.list_items)


      

    

    console.error "RADAR udpate list item"
    console.log e.detail
    dispatch "update", e.detail



  destroyDispatcher = (e)->
    dispatch "destroy", e.detail
    showDeleteConfirmation = false


  # TODO: copy list rendering structure of LIST._list_items
</script>


{#each list.list_items as listItem, index}
  {#if !listItem.is_collapsed || (listItem.ancestry.length == 0)}
    <ListHoverableContainer id={listItem._id} index={listItem.index} ancestry={JSON.stringify(listItem.ancestry)} menuVisibleOnHover={true}>
      <div type="radar-item">
        <ListItemRow item={listItem} context="radar"  classes="radar-item" on:update={updateListItem}/>
  
      </div>
      <div slot="menu">
        <ListItemMenu item={listItem} on:update={updateListItem} on:destroy={destroyDispatcher} context="radar"/>
      </div>
    </ListHoverableContainer>
  {/if}
{/each}


<style lang="sass">

  [type="radar-item"]

    background: transparent

    [item-interior]
      width: calc(100%)
      max-width: calc(100%)

    [collapsible], [menu], [progress] // set cell wdiths
      width: 32px


    [menu]
      text-align: center

    box-sizing: border-box

    padding: 4px 0px

    

    .slot
      width: 100%
      max-width: 100%





    &:hover


      [handle], [collapsible], [draggable-item-menu]
        svg, svg.menu
          display: inline


      [collapsible]
        button[icon]
          padding: 0px !important
        svg
          path.fill
            fill: var(--light-gray)
        &:hover
          svg
            path
              fill: var(--blue)


</style>
