// requires global variable db, declared in App.svelte
// each item is a document in pouchdb
var List, ListItem, PouchDoc;

ListItem = require("./ListItem.js");

PouchDoc = require("../PouchDb/pouch_doc.js");

List = (function() {
  class List extends PouchDoc {
    // attributes starting with _ are managed and need to be updated every time a change has been made

    // array version of list items

    // this is not a function because of Svelte

    // convert list_items from an object to an array -- from pouch
    after_construct(model) {
      var that;
      that = this;
      // if passed an object, assign it to the current item
      //console.error "NEW ROOT ITEM CONSTRUCT"
      //console.log model
      this["list_items"] = [];
      this["list_items_raw"] = [];
      if (model != null) {
        model["doctype"] = "List";
        return this.depouchify(model);
      }
    }

    setRaw(o) {
      return this.list_items_raw = o;
    }

    default_value_for(key) {
      var output;
      output = null;
      switch (key) {
        case "id":
        case "_id":
          output = uuid();
          break;
        case "is_expanded":
          output = {
            list: true,
            radar: true
          };
          break;
        case "is_ongoing":
        case "is_archived":
        case "is_favorite":
          output = false;
          break;
        case "progress":
          output = {
            percent_complete: 0,
            color: "light-gray"
          };
          break;
        case "index":
        case "model_version":
          output = 0;
          break;
        case "list_items":
        case "labels":
        case "ancestry":
          output = [];
          break;
        case "settings":
          output = {
            // importable from other projects
            is_expanded: {
              list: true,
              radar: true
            },
            user_defined_data: [{}],
            // importable from other projects
            labels: [
              {
                value: "unique",
                foreground: "",
                background: "",
                icon: "" // structure tbd
              }
            ]
          };
      }
      //...
      return output;
    }

    /*

    RADAR

    */
    /*

    LIST ITEM HANDLING

    */
    // add from click on divider
    insert_item(item, scb, ecb) {
      var newListItem, parent;
      item["list_id"] = this._id;
      
      // the passed item from the event is not a ListItem instance
      newListItem = new ListItem(item);
      this.list_items_raw.append(newListItem);
      if (newListItem.ancestry.length === 0) {
        this.list_items.append(newListItem);
      } else {
        parent = this.list_items_raw.find({
          _id: newListItem.ancestry.last()
        });
        if (parent != null) {
          parent.list_items.append(newListItem);
        }
      }
      return newListItem.save(scb);
    }

    /*

     _     _                         _           
    | |__ (_) ___ _ __ __ _ _ __ ___| |__  _   _ 
    | '_ \| |/ _ \ '__/ _` | '__/ __| '_ \| | | |
    | | | | |  __/ | | (_| | | | (__| | | | |_| |
    |_| |_|_|\___|_|  \__,_|_|  \___|_| |_|\__, |
                                       |___/ 

     */
    get_children(item, scb, ecb) {
      return db.find({
        selector: {
          ancestry: {
            $all: [item._id]
          }
        }
      }).then(function(result) {
        if (scb != null) {
          return scb(result.docs);
        }
      });
    }

    get_siblings(item, scb, ecb) {
      return db.find({
        selector: {
          ancestry: {
            $eq: item.ancestry
          }
        }
      }).then(function(result) {
        if (scb != null) {
          return scb(result.docs);
        }
      });
    }

    /*

            _     _ _                 
           | |   (_) |                
    _ __   ___  ___| |_   _| |_ ___ _ __ ___  
     | '_ \ / _ \/ __| __| | | __/ _ \ '_ ` _ \ 
     | | | |  __/\__ \ |_  | | ||  __/ | | | | |
     |_| |_|\___||___/\__| |_|\__\___|_| |_| |_|
             ______                   
            |______|                  

    todo: split out the list manipulation from the bulk save so a UI repaint can be done

     */
    nest_item(origin, destination) {
      var changes, get_children, itemsToChange, lastSibling, newAncestry, newIndex, newSiblings, siblingsMaxIndex, that;
      //console.error "List.nest_item"

      //console.log "origin"
      //console.debug origin

      //console.log "destination"
      //console.debug destination
      that = this;
      get_children = this.get_children; // to avoid reference issues
      newAncestry = destination.ancestry.clone().append(destination._id);
      newIndex = 0;
      newSiblings = that.list_items.filter(function(o) {
        return o.ancestry.isEqual(newAncestry);
      });
      if (newSiblings.length > 0) {
        lastSibling = newSiblings.max("index");
        newIndex = lastSibling.index + 1000;
      }
      // getChildren
      siblingsMaxIndex = 0;
      itemsToChange = [];
      changes = {};
      itemsToChange = that.list_items.filter(function(o) {
        return (o._id === origin._id) || (o.ancestry.includes(origin._id));
      });
      //console.error "itemsToChange"
      //console.debug itemsToChange

      //console.log "new Ancestry: #{JSON.stringify(newAncestry)}"
      itemsToChange.forEach(function(item) {
        if (item._id === origin._id) { // parent item
          return Object.merge(item, {
            ancestry: newAncestry.clone(),
            index: newIndex
          });
        } else {
          return Object.merge(item, {
            ancestry: newAncestry.clone().append(origin._id).append(item.ancestry.from(item.ancestry.findIndex(origin._id) + 1))
          });
        }
      });
      return itemsToChange;
    }

    
    ////  _           _ _     ____ _                            
    //// | |__  _   _| | | __/ ___| |__   __ _ _ __   __ _  ___ 
    //// | '_ \| | | | | |/ / |   | '_ \ / _` | '_ \ / _` |/ _ \
    //// | |_) | |_| | |   <| |___| | | | (_| | | | | (_| |  __/
    //// |_.__/ \__,_|_|_|\_\\____|_| |_|\__,_|_| |_|\__, |\___|
    ////                                             |___/      
    bulk_change_list_items(itemsToChange, scb, ecb, fcb) {
      var bulkChangeIds, changes;
      bulkChangeIds = itemsToChange.map(function(item) {
        return {
          _id: item._id
        };
      });
      console.log("bulkChangeIds");
      console.debug(bulkChangeIds);
      changes = itemsToChange.groupBy("_id");
      // flatten each key
      Object.keys(changes).forEach(function(key) {
        return changes[key] = changes[key].first();
      });
      //console.error "write nest changes"
      //console.debug bulkChangeIds
      return db.find({
        selector: {
          $or: bulkChangeIds
        }
      }).then(function(result) {
        var changeQueue;
        // console.log "---------------------------------"
        //console.error "docs to change"
        //console.debug result.docs
        changeQueue = result.docs.map(function(dbDoc) {
          var changesForThisDoc;
          changesForThisDoc = changes[dbDoc._id];
          return Object.merge(dbDoc, changesForThisDoc);
        });
        //console.error "CHANGEQUEUE"
        console.table(changeQueue.map(function(i) {
          return {
            _id: i._id,
            name: i.name,
            ancestry: JSON.stringify(i.ancestry),
            _rev: i._rev
          };
        }));
        return db.bulkDocs(changeQueue).then(function(result) {
          console.error("nest_item completd");
          console.log(result);
          if (scb != null) {
            return scb(result);
          }
        });
      }).catch(function(err) {
        console.log("write nest cahnges error");
        return console.log(err);
      });
    }

    //-----------------------------------------------

    // db

    //-----------------------------------------------

    ////                 _   
    ////  ___  ___  _ __| |_ 
    //// / __|/ _ \| '__| __|
    //// \__ \ (_) | |  | |_ 
    //// |___/\___/|_|   \__|
    ////                     
    /*

    */
    sort() {
      var allParentKeys, ancestryKeys, collapsedIds, collapsedItems, listItemsGroupedyAncestry, sortedList, that;
      that = this;
      sortedList = [];
      allParentKeys = [];
      // group by ancestry length, as we want to add from smallest ancestry to largest
      listItemsGroupedyAncestry = this.list_items_raw.groupBy(function(i) {
        return i.ancestry.length;
      });
      ancestryKeys = Object.keys(listItemsGroupedyAncestry).sortBy(function(key) {
        return parseInt(key);
      });
      ancestryKeys.forEach(function(key) {
        if (key !== "0") { // roots
          return listItemsGroupedyAncestry[key] = listItemsGroupedyAncestry[key].groupBy(function(j) {
            return j.ancestry.last();
          });
        }
      });
      ancestryKeys.forEach(function(ancestryKey) {
        var ancestryGroup, parentKeys;
        if (ancestryKey === "0") { // roots
          return sortedList.append(listItemsGroupedyAncestry[ancestryKey].sortBy("index"));
        } else {
          ancestryGroup = listItemsGroupedyAncestry[ancestryKey];
          parentKeys = Object.keys(ancestryGroup);
          allParentKeys.append(parentKeys);
          that.parent_keys.append(Object.keys(ancestryGroup));
          return parentKeys.forEach(function(parentKey) {
            var insertionIndex;
            insertionIndex = sortedList.findIndex({
              _id: parentKey
            }) + 1;
            return sortedList.insert(ancestryGroup[parentKey].sortBy("index"), insertionIndex);
          });
        }
      });
      // post process
      // - set is parent, set is expanded

      // bulk process expanded
      collapsedItems = sortedList.filter(function(p) {
        return p.is_expanded.list === false;
      });
      collapsedIds = collapsedItems.map("_id");
      sortedList.forEach(function(o) {
        if (allParentKeys.includes(o._id)) {
          o.is_parent = true;
        }
        return o.is_collapsed = collapsedIds.intersect(o.ancestry).length > 0;
      });
      return this.list_items = sortedList;
    }

    sorted() {
      var allParentKeys, ancestryKeys, collapsedIds, collapsedItems, listItemsGroupedyAncestry, sortedList, that;
      that = this;
      sortedList = [];
      allParentKeys = [];
      // group by ancestry length, as we want to add from smallest ancestry to largest
      listItemsGroupedyAncestry = this.list_items_raw.groupBy(function(i) {
        return i.ancestry.length;
      });
      ancestryKeys = Object.keys(listItemsGroupedyAncestry).sortBy(function(key) {
        return parseInt(key);
      });
      ancestryKeys.forEach(function(key) {
        if (key !== "0") { // roots
          return listItemsGroupedyAncestry[key] = listItemsGroupedyAncestry[key].groupBy(function(j) {
            return j.ancestry.last();
          });
        }
      });
      ancestryKeys.forEach(function(ancestryKey) {
        var ancestryGroup, parentKeys;
        if (ancestryKey === "0") { // roots
          return sortedList.append(listItemsGroupedyAncestry[ancestryKey].sortBy("index"));
        } else {
          ancestryGroup = listItemsGroupedyAncestry[ancestryKey];
          parentKeys = Object.keys(ancestryGroup);
          allParentKeys.append(parentKeys);
          that.parent_keys.append(Object.keys(ancestryGroup));
          return parentKeys.forEach(function(parentKey) {
            var insertionIndex;
            insertionIndex = sortedList.findIndex({
              _id: parentKey
            }) + 1;
            return sortedList.insert(ancestryGroup[parentKey].sortBy("index"), insertionIndex);
          });
        }
      });
      // post process
      // - set is parent, set is expanded

      // bulk process expanded
      collapsedItems = sortedList.filter(function(p) {
        return p.is_expanded.list === false;
      });
      collapsedIds = collapsedItems.map("_id");
      sortedList.forEach(function(o) {
        if (allParentKeys.includes(o._id)) {
          o.is_parent = true;
        }
        return o.is_collapsed = collapsedIds.intersect(o.ancestry).length > 0;
      });
      return sortedList;
    }

    set_expanded(id, state) {
      return this.list_items.forEach(function(o) {
        if (o.ancestry.includes(id)) {
          return o.is_collapsed = !state;
        }
      });
    }

    
    //console.log "collapse: #{o._id} #{o.name} #{!state}"
    //console.log o
    list_item_is_visible(id) {}

    /*
           _                  
       ___| | ___  _ __   ___ 
      / __| |/ _ \| '_ \ / _ \
     | (__| | (_) | | | |  __/
      \___|_|\___/|_| |_|\___|

     */
    clone(thenCb) {
      var sourceSelector, that;
      that = this;
      sourceSelector = {
        $or: [
          {
            _id: that._id
          },
          {
            list_id: that._id
          }
        ]
      };
      return db.find({
        selector: sourceSelector
      }).then(function(result) {
        var clonedDocs, newKeys, newList, newListId, originalList, originalListItems;
        originalList = result.docs.find(function(o) {
          return o.doctype === "List";
        });
        originalListItems = result.docs.filter(function(o) {
          return o.doctype === "ListItem";
        });
        // set the index depending on whether it's current or archived
        newKeys = {};
        clonedDocs = [];
        console.error("result.docs");
        console.log(result.docs);
        delete originalList.doctype;
        newList = new List(originalList);
        newListId = uuid();
        newList["id"] = newListId;
        newList["_id"] = newListId;
        newList.name = `${newList.name} copy`;
        //console.error 'NEW LIST'
        //console.log newList.pouchify()
        clonedDocs.append(newList.pouchify());
        originalListItems.sortBy(function(doc) {
          return doc.ancestry.length;
        });
        originalListItems.forEach(function(doc) {
          var newListItem;
          newKeys[doc._id] = uuid();
          newListItem = new ListItem(doc);
          newListItem.list_id = newListId;
          newListItem._id = newKeys[doc._id];
          newListItem._id = newKeys[doc._id];
          newListItem.ancestry = doc.ancestry.map(function(ancestor) {
            if (newKeys[ancestor]) {
              return newKeys[ancestor];
            } else {
              return ancestor;
            }
          });
          return clonedDocs.append(newListItem.pouchify());
        });
        console.error("clonedDocs");
        console.debug(clonedDocs);
        return db.bulkDocs(clonedDocs).then(function(result) {
          //console.log "bulk docs result"
          //console.debug result
          if (thenCb != null) {
            return thenCb(newListId); // for redirecting
          }
        }).catch(function(err) {
          console.log("bulk insert error");
          return console.log(err);
        }).finally(function() {
          return console.log("finally");
        });
      }).catch(function(err) {
        if (typeof catchCb !== "undefined" && catchCb !== null) {
          return catchCb(err);
        } else {
          console.log("select  err");
          return console.log(err);
        }
      }).finally(function() {
        if (typeof finallyCb !== "undefined" && finallyCb !== null) {
          return finallyCb();
        }
      });
    }

    ////  _                          _                   _             
    //// | |__   ___  _   _ ___  ___| | _____  ___ _ __ (_)_ __   __ _ 
    //// | '_ \ / _ \| | | / __|/ _ \ |/ / _ \/ _ \ '_ \| | '_ \ / _` |
    //// | | | | (_) | |_| \__ \  __/   <  __/  __/ |_) | | | | | (_| |
    //// |_| |_|\___/ \__,_|___/\___|_|\_\___|\___| .__/|_|_| |_|\__, |
    ////                                          |_|            |___/ 

    // override if any attributes from the database need deleting, etc.
    housekeeping(model) {
      var deletionKeys;
      deletionKeys = ["is_expanded_radar", "level", "list_items", "description"];
      deletionKeys.forEach(function(key) {
        if (Object.has(model, key)) {
          return delete model[key];
        }
      });
      return model;
    }

    ////            _                 _                    _                 __            
    ////  _ __ ___ (_) __ _ _ __ __ _| |_ ___  __   ____ _| |_   _  ___     / _| ___  _ __ 
    //// | '_ ` _ \| |/ _` | '__/ _` | __/ _ \ \ \ / / _` | | | | |/ _ \   | |_ / _ \| '__|
    //// | | | | | | | (_| | | | (_| | ||  __/  \ V / (_| | | |_| |  __/   |  _| (_) | |   
    //// |_| |_| |_|_|\__, |_|  \__,_|\__\___|___\_/ \__,_|_|\__,_|\___|___|_|  \___/|_|   
    ////              |___/                 |_____|                   |_____|              

    // Override value for an attribute - use this to migrate old or invalid data
    migrate_value_for(model, key) {
      var output;
      output = model[key];
      switch (key) {
        case "is_expanded":
          if (model[key] != null) {
            if (Object.isBoolean(model[key])) {
              output = {
                radar: true,
                list: true
              };
            }
          }
          break;
        case "due_date":
          if (model[key] != null) {
            if (model[key].length > 10) {
              output = Date.create(model[key]).format("{yyyy}-{MM}-{dd}");
            }
          }
      }
      return output;
    }

    ////             _   
    ////   __ _  ___| |_ 
    ////  / _` |/ _ \ __|
    //// | (_| |  __/ |_ 
    ////  \__, |\___|\__|
    ////  |___/          

    // Get a list and its list items
    // Arrange the list items in its tree order
    get(id, thenCb, catchCb, finallyCb) {
      var tcb, that;
      console.log(`Fetch ${id}`);
      that = this;
      tcb = function(doc) {
        return that.depouchify(doc);
      };
      //console.log "tbc"
      //console.log doc
      // get children
      return db.find({
        selector: {
          $or: [
            {
              list_id: id
            },
            {
              _id: id
            }
          ]
        }
      }).then(function(result) {
        var list, list_items;
        list = result.docs.find(function(doc) {
          return doc._id === id;
        });
        if (list != null) {
          that.depouchify(list);
          list_items = result.docs.filter(function(doc) {
            return doc.list_id === id;
          });
          that.list_items_raw = list_items.map(function(doc) {
            var listItem;
            listItem = new ListItem(doc);
            // console.error "should have depouchified"
            listItem.list = that;
            return listItem;
          });
          
          // console.debug that

          // Set the collapsed items list
          that.list_items = that.sorted();
          if (thenCb != null) {
            return thenCb(that);
          }
        } else {
          console.log(`No list with id: ${id} found`);
          return console.table(result.docs);
        }
      });
    }

    delete(cb) {
      var that;
      that = this;
      // remove this and its children
      return db.find({
        selector: {
          $or: [
            {
              list_id: that._id
            },
            {
              _id: that._id
            }
          ]
        }
      }).then(function(result) {
        console.error("Deletion queue");
        console.log(result.docs);
        return db.bulkDocs(result.docs.map(function(doc) {
          doc["_deleted"] = true;
          return doc;
        })).then(function(result) {
          console.error("Lists deleted");
          if (cb != null) {
            return cb(result);
          }
        });
      });
    }

  };

  List.prototype.doctype = "List";

  List.prototype.attribute_whitelist = [
    "model_version",
    "doctype",
    "name",
    "description",
    "progress",
    "index",
    "level",
    "is_favorite", // can include archived
    "is_expanded",
    "is_archived",
    "due_date",
    "labels",
    "settings",
    "created_at",
    "updated_at"
  ];

  /*

  #//  _     ___ ____ _____   ___ _____ _____ __  __ ____    _   _      _                     
  #// | |   |_ _/ ___|_   _| |_ _|_   _| ____|  \/  / ___|  | | | | ___| |_ __   ___ _ __ ___ 
  #// | |    | |\___ \ | |    | |  | | |  _| | |\/| \___ \  | |_| |/ _ \ | '_ \ / _ \ '__/ __|
  #// | |___ | | ___) || |    | |  | | | |___| |  | |___) | |  _  |  __/ | |_) |  __/ |  \__ \
  #// |_____|___|____/ |_|___|___| |_| |_____|_|  |_|____/  |_| |_|\___|_| .__/ \___|_|  |___/
  #//                   |_____|                                          |_|                  

   */
  // items where is_expanded.list == false
  List.prototype.collapsed_keys = [];

  
  // keys of items with children
  List.prototype.parent_keys = [];

  return List;

}).call(this);

module.exports = List;
