<script lang="coffeescript">


  #//  _     _     _         ____  _                   
  #// | |   (_)___| |_ ___  / ___|| |__   _____      __
  #// | |   | / __| __/ __| \___ \| '_ \ / _ \ \ /\ / /
  #// | |___| \__ \ |_\__ \_ ___) | | | | (_) \ V  V / 
  #// |_____|_|___/\__|___(_)____/|_| |_|\___/ \_/\_/  
                                                    

  import { onMount } from "svelte"
  import {push, pop, replace, location} from 'svelte-spa-router'
  import {
    FlexColumn, 
    FlexColumnCell, 
    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { 
    debug
    InlineInput 
  } from "../../Braintapper"
  
  import { 
    PouchLastSyncResponse
    List
    ListItem
    PageContent
    PageHeader
    QuickEntry
    DueDatePopover
    ProgressPopover
    ListMenu
    ListDroppableDivider 
  } from "../../Application"

  debug.on = window.debug
  

  import ListItems from './_ListItems.svelte'


  
  export params = {}
  export list = undefined

  


  #//      _                    _              _ ____                    
  #//   __| |_ __ __ _  __ _   / \   _ __   __| |  _ \ _ __ ___  _ __    
  #//  / _` | '__/ _` |/ _` | / _ \ | '_ \ / _` | | | | '__/ _ \| '_ \   
  #// | (_| | | | (_| | (_| |/ ___ \| | | | (_| | |_| | | | (_) | |_) |  
  #//  \__,_|_|  \__,_|\__, /_/   \_\_| |_|\__,_|____/|_|  \___/| .__/   
  #//                  |___/                                    |_|      





  #//             _   _     _     _   
  #//   __ _  ___| |_| |   (_)___| |_ 
  #//  / _` |/ _ \ __| |   | / __| __|
  #// | (_| |  __/ |_| |___| \__ \ |_ 
  #//  \__, |\___|\__|_____|_|___/\__|
  #//  |___/                          

  listModel = new List

  getList = ()-> # get list from db and refresh local view

    console.warn "getList fired"
    

    scb = (listObject) ->
      console.warn "getList scb"
      console.debug listObject
      list = listObject
      #console.log "collapsed"
      #console.log list.collapsed_keys
      #console.log "parents"
      #console.log list.parent_keys      


    listModel.get(params.id, scb, null, null)




  #//        _                  _     _     _   
  #//    ___| | ___  _ __   ___| |   (_)___| |_ 
  #//   / __| |/ _ \| '_ \ / _ \ |   | / __| __|
  #//  | (__| | (_) | | | |  __/ |___| \__ \ |_ 
  #//   \___|_|\___/|_| |_|\___|_____|_|___/\__|
  #//  


  cloneList = (e)->
    console.error "cloneList"

    scb = (response)->
      console.error "cloneList.scb"
      console.log response
      push "/lists/#{response}"

    console.error "clone listmodel"
    console.debug listModel
    listModel.clone(scb)
  
  #//                  _       _       ____             ____        _       
  #//  _   _ _ __   __| | __ _| |_ ___|  _ \ _   _  ___|  _ \  __ _| |_ ___ 
  #// | | | | '_ \ / _` |/ _` | __/ _ \ | | | | | |/ _ \ | | |/ _` | __/ _ \
  #// | |_| | |_) | (_| | (_| | ||  __/ |_| | |_| |  __/ |_| | (_| | ||  __/
  #//  \__,_| .__/ \__,_|\__,_|\__\___|____/ \__,_|\___|____/ \__,_|\__\___|
  #//       |_|                                                             

  updateDueDate = (e)->

    list.due_date = e.detail
    list = list # force refresh
    updateListAttribute {
      detail:
        _id: list._id
        due_date: Date.create(e.detail).format("{yyyy}-{MM}-{dd}") 
    }

  #//                  _       _       _     _     _      _   _   _        _ _           _       
  #//  _   _ _ __   __| | __ _| |_ ___| |   (_)___| |_   / \ | |_| |_ _ __(_) |__  _   _| |_ ___ 
  #// | | | | '_ \ / _` |/ _` | __/ _ \ |   | / __| __| / _ \| __| __| '__| | '_ \| | | | __/ _ \
  #// | |_| | |_) | (_| | (_| | ||  __/ |___| \__ \ |_ / ___ \ |_| |_| |  | | |_) | |_| | ||  __/
  #//  \__,_| .__/ \__,_|\__,_|\__\___|_____|_|___/\__/_/   \_\__|\__|_|  |_|_.__/ \__,_|\__\___|
  #//       |_|                                                                                  


  updateListAttribute = (e)->
    console.error "updateListAttribute"
    console.debug e.detail
    changes = e.detail
    scb = ()->
      Object.keys(changes).forEach (key)->
        listModel[key] = changes[key]
      list = list

    listModel.patch e.detail, scb



  #//                  _       _       _     _     _   ___ _                 
  #//  _   _ _ __   __| | __ _| |_ ___| |   (_)___| |_|_ _| |_ ___ _ __ ___  
  #// | | | | '_ \ / _` |/ _` | __/ _ \ |   | / __| __|| || __/ _ \ '_ ` _ \ 
  #// | |_| | |_) | (_| | (_| | ||  __/ |___| \__ \ |_ | || ||  __/ | | | | |
  #//  \__,_| .__/ \__,_|\__,_|\__\___|_____|_|___/\__|___|\__\___|_| |_| |_|
  #//       |_|                                                              

  updateListItem = (e)-> # patch a child in the list_items hierarchy
    console.error "List.Show.svelte: updateListItem"
    console.log e.detail

    found = theList.list_items.find { _id: e.detail._id }

    if found?
      found.patch e.detail, getList
    else
      console.error "item not found"



                                                            
  #//            _     _ _     _     _   ___ _               _____     ____             _     
  #//   __ _  __| | __| | |   (_)___| |_|_ _| |_ ___ _ __ __|_   _|__ |  _ \ ___   ___ | |_   
  #//  / _` |/ _` |/ _` | |   | / __| __|| || __/ _ \ '_ ` _ \| |/ _ \| |_) / _ \ / _ \| __|  
  #// | (_| | (_| | (_| | |___| \__ \ |_ | || ||  __/ | | | | | | (_) |  _ < (_) | (_) | |_   
  #//  \__,_|\__,_|\__,_|_____|_|___/\__|___|\__\___|_| |_| |_|_|\___/|_| \_\___/ \___/ \__|  
                                                                                           

  addListItemToRoot = (event)-> # add to list root
    console.error "addListItemToRoot"

    rootItems = list.list_items_raw.filter (o)->
      return o.ancestry.length == 0
    
    if rootItems.length > 0
      maxIndex = rootItems.max (o)->
        return o.index
      maxIndex = maxIndex.index    
    else
      maxIndex = 0
    
    newListItem = new ListItem { list_id: list._id, ancestry: [], name: event.detail.name, index: maxIndex + 1000, list: listModel }
    console.log "newLIstItem"
    console.log newListItem
    
    list.list_items_raw.append newListItem
    list.sort()
    list.list_items = list.list_items

    saveCallback = ()->
      
      getList()

    newListItem.post saveCallback




  #//            _     _ _     _     _   ___ _                 
  #//   __ _  __| | __| | |   (_)___| |_|_ _| |_ ___ _ __ ___  
  #//  / _` |/ _` |/ _` | |   | / __| __|| || __/ _ \ '_ ` _ \ 
  #// | (_| | (_| | (_| | |___| \__ \ |_ | || ||  __/ | | | | |
  #//  \__,_|\__,_|\__,_|_____|_|___/\__|___|\__\___|_| |_| |_|
  #//                                                          

  addListItem = (event)->

    console.error "ADD LIST ITEM"
    console.log event.detail

    newListItem = new ListItem { 
      list_id: list._id
      name: event.detail.name || "New item created at #{Date.create().format('{HH}:{mm}:{ss}')}"
      list: listModel
      ancestry: event.detail.ancestry
      index: event.detail.index }


    list.list_items_raw.append newListItem
    list.sort()
    list.list_items = list.list_items
    



    scb = (postResponse)->
      # newListItem has no id, so give it one
      console.log "addListItem scb"
      console.error postResponse     
      newListItem._id = postResponse._id
      # newListItem._id = postResponse._id


      # listModel.list_items_raw.append newListItem

        
      
      getList()

    newListItem.post scb


  #//      _           _                   _     _     _   ___ _                 
  #//   __| | ___  ___| |_ _ __ ___  _   _| |   (_)___| |_|_ _| |_ ___ _ __ ___  
  #//  / _` |/ _ \/ __| __| '__/ _ \| | | | |   | / __| __|| || __/ _ \ '_ ` _ \ 
  #// | (_| |  __/\__ \ |_| | | (_) | |_| | |___| \__ \ |_ | || ||  __/ | | | | |
  #//  \__,_|\___||___/\__|_|  \___/ \__, |_____|_|___/\__|___|\__\___|_| |_| |_|
  #//                                |___/                                       

  destroyListItem = (e)->
    console.error "destroyListItem"
    
    listItem = e.detail

    # wipe from UI

    #deletionGroup = list

    console.log e.detail


    listItem.delete getList



  # //       _                  _     _     _   ___ _                 
  # //   ___| | ___  _ __   ___| |   (_)___| |_|_ _| |_ ___ _ __ ___  
  # //  / __| |/ _ \| '_ \ / _ \ |   | / __| __|| || __/ _ \ '_ ` _ \ 
  # // | (__| | (_) | | | |  __/ |___| \__ \ |_ | || ||  __/ | | | | |
  # //  \___|_|\___/|_| |_|\___|_____|_|___/\__|___|\__\___|_| |_| |_|


  cloneListItem = (e, scb)->

    found = theList.list_items_raw.find { _id: e.detail._id }
    
    if found?
      siblings = theList.list_items_raw.filter (o)->
        o.ancestry.isEqual(found.ancestry)

      maxIndex = siblings.max("index")

      cloneOptions = 
        index: maxIndex.index + 1000

      found.clone cloneOptions, getList


  #//       _                  _     _     _   ___ _               _____     
  #//   ___| | ___  _ __   ___| |   (_)___| |_|_ _| |_ ___ _ __ __|_   _|__  
  #//  / __| |/ _ \| '_ \ / _ \ |   | / __| __|| || __/ _ \ '_ ` _ \| |/ _ \ 
  #// | (__| | (_) | | | |  __/ |___| \__ \ |_ | || ||  __/ | | | | | | (_) |
  #//  \___|_|\___/|_| |_|\___|_____|_|___/\__|___|\__\___|_| |_| |_|_|\___/ 
  #//                                                                        

  cloneListItemTo = (e, scb)->
    console.error "cloneListItemTo"
    console.log e.detail
    window.raw = theList.list_items_raw
    found = theList.list_items_raw.find({ _id: e.detail._id })

    console.log "found item?"
    console.log found


    if found?

    
      cloneOptions = 
        list_id: e.detail.to._id

      console.error "Clone TO:"
      console.log e.detail

      found.clone cloneOptions, getList


  #//                           _     _     _   ___ _               _____     
  #//  _ __ ___   _____   _____| |   (_)___| |_|_ _| |_ ___ _ __ __|_   _|__  
  #// | '_ ` _ \ / _ \ \ / / _ \ |   | / __| __|| || __/ _ \ '_ ` _ \| |/ _ \ 
  #// | | | | | | (_) \ V /  __/ |___| \__ \ |_ | || ||  __/ | | | | | | (_) |
  #// |_| |_| |_|\___/ \_/ \___|_____|_|___/\__|___|\__\___|_| |_| |_|_|\___/ 
  #//                                                                         

  moveListItemTo = (e, scb)->
    console.error "moveListItemTo"
    console.log e.detail
    window.raw = theList.list_items_raw
    found = theList.list_items_raw.find({ _id: e.detail._id })

    console.log "found item?"
    console.log found


    if found?

    
      moveOptions = 
        list_id: e.detail.to._id

      console.error "Clone TO:"
      console.log e.detail

      found.move moveOptions, getList

   #//      _           _                   _     _     _   
   #//   __| | ___  ___| |_ _ __ ___  _   _| |   (_)___| |_ 
   #//  / _` |/ _ \/ __| __| '__/ _ \| | | | |   | / __| __|
   #// | (_| |  __/\__ \ |_| | | (_) | |_| | |___| \__ \ |_ 
   #//  \__,_|\___||___/\__|_|  \___/ \__, |_____|_|___/\__|
   #//                                |___/                 

  destroyList = (e)->
    console.error "DESOTRY LIST"
    scb = ()->
      push "/lists"
    listModel.delete scb


  
  #

  # Drag Drop

  #
  #      _ _       _     _           ____                  _   _                 _ _           
  #   __| (_)_   _(_) __| | ___ _ __|  _ \ _ __ ___  _ __ | | | | __ _ _ __   __| | | ___ _ __ 
  #  / _` | \ \ / / |/ _` |/ _ \ '__| | | | '__/ _ \| '_ \| |_| |/ _` | '_ \ / _` | |/ _ \ '__|
  # | (_| | |\ V /| | (_| |  __/ |  | |_| | | | (_) | |_) |  _  | (_| | | | | (_| | |  __/ |   
  #  \__,_|_| \_/ |_|\__,_|\___|_|  |____/|_|  \___/| .__/|_| |_|\__,_|_| |_|\__,_|_|\___|_|   
  #                                                 |_|                                        
  #

  # todo: do some ui painting before trickling up


  ###
    changedItem = dragItem
    if event.dropItem.nest?
      if event.dropItem.nest
        changedItem.ancestry = dragItem.ancestry.append(dragItem._id)
      else # move-to
        changedItem.ancestry = dragItem.ancestry



  ###


  dividerDropHandler = (event)->

    detail = event.detail

    affectedItems = list.list_items_raw.filter (item)->
      return (item._id == detail.dragItem._id) || item.ancestry.includes(detail.dragItem._id)

    nextInQueue = ()->
      if affectedItems.length > 0
        
        currentChange = affectedItems.shift()
        console.error "Processing #{currentChange.name}, #{affectedItems.length} items remaining"
        console.log currentChange
        if currentChange._id == detail.dragItem._id
          currentChange.patch {
            _id: currentChange._id
            ancestry: detail.dropItem.ancestry # add the additional id for the ancestry in the html markup
            index: detail.dropItem.index
          }, nextInQueue
        else
          
          currentChange.patch {
            _id: currentChange._id
            ancestry: detail.dropItem.ancestry.clone().append(currentChange.ancestry.from(currentChange.ancestry.findIndex(detail.dragItem._id) ))
          }, nextInQueue
      else
        # done
        list.sort()
        list = list
        getList()

    nextInQueue()





  #//                  _   _   _                 _ _           
  #//  _ __   ___  ___| |_| | | | __ _ _ __   __| | | ___ _ __ 
  #// | '_ \ / _ \/ __| __| |_| |/ _` | '_ \ / _` | |/ _ \ '__|
  #// | | | |  __/\__ \ |_|  _  | (_| | | | | (_| | |  __/ |   
  #// |_| |_|\___||___/\__|_| |_|\__,_|_| |_|\__,_|_|\___|_|   
  #//                                                          


  nestHandler = (event)->

    ecb = (e)->
      console.log "nesthandler error"
      console.log e

  

    listModel.bulk_change_list_items event.detail, getList, ecb


  #//  _                            _    ____ _ _       _                         _ 
  #// (_)_ __ ___  _ __   ___  _ __| |_ / ___| (_)_ __ | |__   ___   __ _ _ __ __| |
  #// | | '_ ` _ \| '_ \ / _ \| '__| __| |   | | | '_ \| '_ \ / _ \ / _` | '__/ _` |
  #// | | | | | | | |_) | (_) | |  | |_| |___| | | |_) | |_) | (_) | (_| | | | (_| |
  #// |_|_| |_| |_| .__/ \___/|_|   \__|\____|_|_| .__/|_.__/ \___/ \__,_|_|  \__,_|
  #//             |_|                            |_|                                

  importClipboard = (e)->
    console.error "importClipboard"
    rootItems = list.list_items_raw.filter (o)->
      return o.ancestry.length == 0
    
    if rootItems.length > 0
      maxIndex = rootItems.max (o)->
        return o.index
      maxIndex = maxIndex.index
    else
      maxIndex = 0
    console.error "maxindex: #{maxIndex}"

    parsedLines = e.detail.list_items.lines().filter (o)->
      return o.length > 0
    
    console.log parsedLines

    newListItems = parsedLines.map (line, index)->
      modelized = new ListItem { list_id: e.detail._id,  name: line, ancestry: [], index: (maxIndex + ((index + 1) * 1000)) }
      return modelized.pouchify()
    
    console.log newListItems
    db.bulkDocs newListItems
    .then (result)->
      console.error "importClipboard - inserted #{newListItems.length}"
      console.log result
      getList()



  #//  _                            _    ____ _ _       _                         _ _____     _     _     _   ___ _                 
  #// (_)_ __ ___  _ __   ___  _ __| |_ / ___| (_)_ __ | |__   ___   __ _ _ __ __| |_   _|__ | |   (_)___| |_|_ _| |_ ___ _ __ ___  
  #// | | '_ ` _ \| '_ \ / _ \| '__| __| |   | | | '_ \| '_ \ / _ \ / _` | '__/ _` | | |/ _ \| |   | / __| __|| || __/ _ \ '_ ` _ \ 
  #// | | | | | | | |_) | (_) | |  | |_| |___| | | |_) | |_) | (_) | (_| | | | (_| | | | (_) | |___| \__ \ |_ | || ||  __/ | | | | |
  #// |_|_| |_| |_| .__/ \___/|_|   \__|\____|_|_| .__/|_.__/ \___/ \__,_|_|  \__,_| |_|\___/|_____|_|___/\__|___|\__\___|_| |_| |_|
  #//             |_|                            |_|                                                                                

  importClipboardToListItem = (e)->
    console.error "importClipboardToListItem"

    indexOverride = Object.has(e.detail, "index") # then index override
      
    parsedLines = e.detail.list_items.lines().filter (o)->
      return o.length > 0
    
    console.log parsedLines



    siblingItems = list.list_items_raw.filter (o)->
      return o.ancestry.isEqual e.detail.ancestry
    siblingItems.sortBy "index"





    indexIncrement = 1000

    if siblingItems.length > 0
      # need to check before and after

      if indexOverride

        console.error "INDEX OVERRIDE"
        thisIndex = siblingItems.findIndex { _id: e.detail._id }

        console.error "ThisINdex #{thisIndex}"
        console.log siblingItems[thisIndex]

        if thisIndex >= 0

          switch thisIndex
            when 0 # first or (first and last)
              baseIndex = e.detail.index - (parsedLines * 1000) 


            when (siblingItems.length - 1) # last

              baseIndex = e.detail.index

            else  # "somewhere in between"
              console.error "INDEX OVERRIDE BETWEEN 2 ITEMS [#{thisIndex}]"
              previousItem = siblingItems[thisIndex]
              nextItem = siblingItems[thisIndex + 1]
              console.log siblingItems
              console.debug previousItem
              console.debug nextItem
              startIndex = e.detail.index
              console.error "startIndex #{startIndex}"
              nextIndex = nextItem.index
              console.error "nextIndex #{nextIndex}"
              if nextIndex == startIndex # yikes
                console.error "YIKES CASE!"
                # fuck it, make it all the same index
                baseIndex = nextIndex
                indexIncrement = 0
              else
                indexGap = nextIndex - startIndex
                console.warn "indexGap #{indexGap}"
                buffer = indexGap * 0.1 # 10 percent
                bufferRange = (nextIndex - buffer) - (startIndex)
                console.warn "BUFFER RANGE: #{(nextIndex - buffer)} - #{(startIndex)} = #{bufferRange}"
                
                indexIncrement = bufferRange / parsedLines.length
                
                baseIndex = startIndex 

                console.warn "indexIncrement: #{indexIncrement}"
                console.warn "baseIndex: #{baseIndex}"

        baseIndex = e.detail.index


      else
        console.error "NO INDEX OVERRIDE"
        baseIndex = siblingItems.max (o)->
          return o.index
        baseIndex = baseIndex.index
    
    
    
    else
      baseIndex = 0


    


    console.error "baseIndex: #{baseIndex}"



    newListItems = parsedLines.map (line, index)->
      modelized = new ListItem { list_id: list._id,  name: line, ancestry: e.detail.ancestry, index: (baseIndex + ((index + 1) * indexIncrement)) }
      return modelized.pouchify()
    
    console.log newListItems
    
    db.bulkDocs newListItems
    .then (result)->
      console.error "importClipboard - inserted #{newListItems.length}"
      console.log result
      getList()
    





  #//              __  __                   _   
  #//   ___  _ __ |  \/  | ___  _   _ _ __ | |_ 
  #//  / _ \| '_ \| |\/| |/ _ \| | | | '_ \| __|
  #// | (_) | | | | |  | | (_) | |_| | | | | |_ 
  #//  \___/|_| |_|_|  |_|\___/ \__,_|_| |_|\__|
  #//                                           


  onMount ()->
    PouchLastSyncResponse.subscribe (response)->
      unless response.handled
        console.error "PouchLastSyncResponse changed"
        console.log response      
        if response.direction == "pull"
          console.log "a pull occurred"
          pulledDocs = response.change.docs

          if pulledDocs.length > 0
            console.log "some docs were pulled, check to see if route was affected"
            affectedDocsForUrl = pulledDocs.filter (o)->
              return (o.list_id == list._id ) || (o._id == list._id)

            console.log "Affected docs = #{affectedDocsForUrl}"
            console.log affectedDocsForUrl      
            console.error "should refresh!"
            getList()
          else
            # No changed documents
            console.info "No sync changes related to this view"
          response.handled = true
          PouchLastSyncResponse.set response

        # todo make this smarter by handling edits, adds and deletions differently




  #//  ____                 _   _           
  #// |  _ \ ___  __ _  ___| |_(_)_   _____ 
  #// | |_) / _ \/ _` |/ __| __| \ \ / / _ \
  #// |  _ <  __/ (_| | (__| |_| |\ V /  __/
  #// |_| \_\___|\__,_|\___|\__|_| \_/ \___|
  #//                                       


  # handle a reroute
  `$: if (params.id) { getList() }`

  `$: title = list ? list.name : "Unnamed list"`

  `$: theList = list`


</script>

<svelte:head>
  <title>Lists :: {title}</title>
</svelte:head>
{#if list}

  <PageContent>

    
    <PageHeader icon={true}>
      <div slot="icon"><ProgressPopover value={list.progress} item={list} on:update={updateListAttribute} size={36}/></div>
      <div>
        <FlexRow style="max-width: 100%;">
          <FlexRowCell flex style="overflow-x: hidden">
            <h1 title>
            <InlineInput item={list} on:update={updateListAttribute} style="margin-left: -2px" inputStyle="margin-top: 2px; margin-left: -1px"/>
            
            </h1>
          </FlexRowCell>
          
          <FlexRowCell style="padding-top: 16px; width: auto; margin-right: 8px" flex="initial">
            <DueDatePopover value={list.due_date} on:update={updateDueDate} on:open on:close />
          </FlexRowCell>        
          <FlexRowCell style="padding-top: 16px; width: 32px" flex="initial">
            <ListMenu item={list} on:select={updateListAttribute} on:clone={cloneList} on:importClipboard={importClipboard} on:destroy={destroyList}/>
          </FlexRowCell>        
        </FlexRow>
      </div>
    </PageHeader>

    
    <hr/>
    
    
    
    <ListItems {list} on:clone={cloneListItem} on:cloneTo={cloneListItemTo} on:moveTo={moveListItemTo} on:importClipboard={importClipboardToListItem} on:update={updateListItem} on:drop={dividerDropHandler} on:dividerDrop={dividerDropHandler} on:nest={nestHandler} on:add={addListItem} on:destroy={destroyListItem} />

    {#if list.is_archived == false}
    <QuickEntry on:add={addListItemToRoot} stubPlaceholder="Add Top Level List Item" />
    {/if}

  
  </PageContent>



  
{/if}
<style lang="sass">

  h6
    text-transform: uppercase
    margin-bottom: -4px
    color: var(--gray)
