var List, ListItem, RadarList, debug;

debug = require("../../Braintapper/Debug/debug.js");

/*

  ____           _            
 |  _ \ __ _  __| | __ _ _ __ 
 | |_) / _` |/ _` |/ _` | '__|
 |  _ < (_| | (_| | (_| | |   
 |_| \_\__,_|\__,_|\__,_|_|   

 */
// list
List = require("./List.js");

ListItem = require("./ListItem.js");

RadarList = (function() {
  class RadarList {
    constructor() {
      this["raw_items"] = [];
      this["lists"] = {};
    }

    sorted(listItems) {
      var allParentKeys, ancestryKeys, collapsedIds, collapsedItems, listItemsGroupedyAncestry, sortedList, that;
      that = this;
      sortedList = [];
      allParentKeys = [];
      // group by ancestry length, as we want to add from smallest ancestry to largest
      listItemsGroupedyAncestry = listItems.groupBy(function(i) {
        return i.ancestry.length;
      });
      ancestryKeys = Object.keys(listItemsGroupedyAncestry).sortBy(function(key) {
        return parseInt(key);
      });
      ancestryKeys.forEach(function(key) {
        if (key !== "0") { // roots
          return listItemsGroupedyAncestry[key] = listItemsGroupedyAncestry[key].groupBy(function(j) {
            return j.ancestry.last();
          });
        }
      });
      ancestryKeys.forEach(function(ancestryKey) {
        var ancestryGroup, parentKeys;
        if (ancestryKey === "0") { // roots
          return sortedList.append(listItemsGroupedyAncestry[ancestryKey].sortBy("index"));
        } else {
          ancestryGroup = listItemsGroupedyAncestry[ancestryKey];
          parentKeys = Object.keys(ancestryGroup);
          allParentKeys.append(parentKeys);
          return parentKeys.forEach(function(parentKey) {
            var insertionIndex;
            insertionIndex = sortedList.findIndex({
              _id: parentKey
            }) + 1;
            return sortedList.insert(ancestryGroup[parentKey].sortBy("index"), insertionIndex);
          });
        }
      });
      // post process
      // - set is parent, set is expanded

      // bulk process expanded
      collapsedItems = sortedList.filter(function(p) {
        return p.is_expanded.radar === false;
      });
      collapsedIds = collapsedItems.map("_id");
      //console.error "collapsedids"
      //console.debug collapsedIds
      sortedList.forEach(function(o) {
        if (allParentKeys.includes(o._id)) {
          o.is_parent = true;
        }
        return o.is_collapsed = collapsedIds.intersect(o.ancestry).length > 0;
      });
      return sortedList;
    }

    /*

           _           _   
    ___  ___| | ___  ___| |_ 
     / __|/ _ \ |/ _ \/ __| __|
     \__ \  __/ |  __/ (__| |_ 
     |___/\___|_|\___|\___|\__|

     */
    select(options, thenCb, catchCb, finallyCb) {
      var archivedSelector, listItems, lists, that;
      // reset

      //console.error "RADAR OPTIONS"
      //console.log JSON.stringify(options,null,2)
      debug.on = true;
      //debug.checkpoint "RadarList.select","-"

      // reset
      this.raw_items = [];
      this.lists = {};
      that = this;
      // TODO: deduplicate selectors when verified working

      // first pass get items that meet criteria
      // second pass, get other items
      // 1) Missing projects - from list items
      // 2) Missing parents, from list items        
      lists = [];
      listItems = [];
      //debug.checkpoint "Start query"
      archivedSelector = {
        selector: {
          $and: [
            {
              doctype: "List"
            },
            {
              is_archived: true
            }
          ]
        }
      };
      return db.find(archivedSelector).then(function(archived) {
        var archivedListIds, date_selector, initialSelector, selectors;
        console.error("Archived");
        console.debug(archived.docs);
        archivedListIds = archived.docs.map(function(doc) {
          return doc._id;
        });
        if (archived.docs.length === 0) {
          selectors = [
            {
              $or: [
                {
                  doctype: {
                    $eq: "List"
                  }
                },
                {
                  //$nin: archivedListIds
                  doctype: {
                    $eq: "ListItem"
                  }
                }
              ]
            }
          ];
        } else {
          //$nin: archivedListIds
          selectors = [
            {
              $or: [
                {
                  $and: [
                    {
                      doctype: "List"
                    },
                    {
                      _id: {
                        $nin: archivedListIds
                      }
                    }
                  ]
                },
                {
                  $and: [
                    {
                      doctype: "ListItem"
                    },
                    {
                      list_id: {
                        $nin: archivedListIds
                      }
                    }
                  ]
                }
              ]
            }
          ];
        }
        switch (options.horizon) {
          case "0d":
            date_selector = {
              due_date: {
                $eq: Date.create("today").format("{yyyy}-{MM}-{dd}")
              }
            };
            break;
          case "1d":
          case "7d":
          case "14d":
            date_selector = {
              due_date: {
                $gte: Date.create(`${parseInt(options.horizon)} days ago`).format("{yyyy}-{MM}-{dd}"),
                $lte: Date.create(`${parseInt(options.horizon)} days from now`).format("{yyyy}-{MM}-{dd}")
              }
            };
            break;
          case "1m":
          case "3m":
          case "6m":
          case "12m":
            date_selector = {
              due_date: {
                $gte: Date.create(`${parseInt(options.horizon)} months ago`, {
                  setUTC: true
                }).format("{yyyy}-{MM}-{dd}"),
                $lte: Date.create(`${parseInt(options.horizon)} months from now`, {
                  setUTC: true
                }).format("{yyyy}-{MM}-{dd}")
              }
            };
            break;
          case "1y":
            date_selector = {
              due_date: {
                $gte: Date.create(`${parseInt(options.horizon)} years ago`, {
                  setUTC: true
                }).format("{yyyy}-{MM}-{dd}"),
                $lte: Date.create(`${parseInt(options.horizon)} years from now`, {
                  setUTC: true
                }).format("{yyyy}-{MM}-{dd}")
              }
            };
            break;
          default:
            date_selector = {
              due_date: {
                $ne: null
              }
            };
        }
        if (options.ongoing_only) {
          selectors.append({
            is_ongoing: {
              $eq: true
            }
          });
          if (!options.include_completed) {
            selectors.append({
              "progress.percent_complete": {
                $lt: 100 // due 
              }
            });
          }
        } else {
          if (!options.include_completed) {
            selectors.append({
              "progress.percent_complete": {
                $lt: 100
              }
            });
          }
          selectors.append(date_selector);
        }
        initialSelector = {
          $and: selectors
        };
        //console.error "initialSelector"
        //console.log JSON.stringify(initialSelector,null,2)
        return db.find({
          selector: initialSelector
        }).then(function(result) {
          var firstPassIds, outputList, secondPassIds, secondSelector;
          //debug.checkpoint "First query: Received Result [#{result.docs.length}]"

          // console.debug result.docs
          // that.items = that.transformCollection(result.docs)
          firstPassIds = [];
          secondPassIds = [];
          outputList = [];
          firstPassIds = result.docs.map(function(doc) {
            return doc._id;
          });
          firstPassIds = firstPassIds.unique(); // remove dupes
          
          // TODO: before first pass - get list of archived lists and exclude them from results

          // First Pass
          result.docs.forEach(function(doc) {
            switch (doc.doctype) {
              case "List":
                return lists.append(new List(doc));
              case "ListItem":
                listItems.append(new ListItem(doc));
                secondPassIds.append(doc.list_id);
                return secondPassIds.append(doc.ancestry);
            }
          });
          secondPassIds = secondPassIds.unique().exclude(function(id) {
            return firstPassIds.includes(id);
          });
          //debug.checkpoint "First query: Finished Processing"
          // second pass
          //debug.checkpoint "Second query: Start Processing"
          if (true) { // remove this later
            //debug.checkpoint "Second query: Post INdex"

            // console.error "last checkpoint before fail?"
            secondSelector = secondPassIds.map(function(id) {
              return {
                _id: id
              };
            });
            
            //console.log "secondPassIds"
            //console.log secondPassIds
            //debug.checkpoint "Second query set selector"
            return db.find({
              selector: {
                
                //$or: secondSelector
                _id: {
                  $in: secondPassIds
                }
              }
            }).then(function(secondaryResults) {
              var groupedListKeys, groupedLists, secondPassListItems, secondPassLists;
              //debug.checkpoint "Second query: Received #{secondaryResults.docs.length} results"
              //console.log secondaryResults.docs
              secondPassLists = secondaryResults.docs.filter(function(o) {
                return o.doctype === "List";
              });
              secondPassListItems = secondaryResults.docs.filter(function(o) {
                return o.doctype === "ListItem";
              });
              lists.append(secondPassLists.map(function(o) {
                return new List(o);
              }));
              listItems.append(secondPassListItems.map(function(o) {
                return new ListItem(o);
              }));
              that.raw_items.append(listItems);
              lists.sortBy("name");
              groupedLists = listItems.groupBy("list_id");
              groupedListKeys = Object.keys(groupedLists);
              groupedListKeys.forEach(function(key) {
                return groupedLists[key] = that.sorted(groupedLists[key]);
              });
              /*
              listItems.forEach (o)->
              o.list = lists.find (o)->
                return o.list_id = o._id
              */
              console.log(lists);
              lists.forEach(function(list) {
                //console.log "listsforeach #{list._id}"
                //console.debug  groupedLists[list._id]
                if (groupedLists[list._id] != null) {
                  return list.list_items = groupedLists[list._id].map(function(o) {
                    o.list = list;
                    return o;
                  });
                } else {
                  return list.list_items = [];
                }
              });
              if (thenCb != null) {
                return thenCb({
                  lists: lists,
                  list_items: groupedLists
                });
              }
            });
          }
        /*
        thenCb outputList.filter (list)->
          return list.is_archived == false
        */
        }).catch(function(err) {
          if (catchCb != null) {
            return catchCb(err);
          } else {
            console.log("select  err");
            return console.log(err);
          }
        }).finally(function() {
          if (finallyCb != null) {
            return finallyCb();
          }
        });
      });
    }

  };

  RadarList.prototype.name = "RadarList";

  return RadarList;

}).call(this);

module.exports = RadarList;
