<script lang="coffeescript">

  import { createEventDispatcher, onMount } from "svelte"
  dispatch = createEventDispatcher()

  import { IsDragging, CurrentlyDragging } from "./stores.js"
  
  import Indent from "braintapper-svelte-indent"
  
  import {
    FlexRow
    FlexRowCell
  } from "braintapper-svelte-flex"

  import { 
    debug
    IconSvg 
    TippyContextMenu
  } from "../../../Braintapper"
  



  # locally tracked store values

  currentlyDragging = undefined
  isDragging = false


  #//              __  __                   _   
  #//   ___  _ __ |  \/  | ___  _   _ _ __ | |_ 
  #//  / _ \| '_ \| |\/| |/ _ \| | | | '_ \| __|
  #// | (_) | | | | |  | | (_) | |_| | | | | |_ 
  #//  \___/|_| |_|_|  |_|\___/ \__,_|_| |_|\__|
  #//                                           

  onMount ()->
    IsDragging.subscribe  (val)->
      isDragging = val
    CurrentlyDragging.subscribe  (val)->
      currentlyDragging = val          




  import Menu from "./_DividerMenu.svelte"


  export index = undefined
  export indentation = 0
  export listItems = undefined
  export item = undefined
  



  #//  _   _                   
  #// | |_(_)_ __  _ __  _   _ 
  #// | __| | '_ \| '_ \| | | |
  #// | |_| | |_) | |_) | |_| |
  #//  \__|_| .__/| .__/ \__, |
  #//       |_|   |_|    |___/ 

  tippyComponent = undefined
  tippyObject = undefined
  

  #//      _        _         _                  _    _             
  #//  ___| |_ __ _| |_ ___  | |_ _ __ __ _  ___| | _(_)_ __   __ _ 
  #// / __| __/ _` | __/ _ \ | __| '__/ _` |/ __| |/ / | '_ \ / _` |
  #// \__ \ || (_| | ||  __/ | |_| | | (_| | (__|   <| | | | | (_| |
  #// |___/\__\__,_|\__\___|  \__|_|  \__,_|\___|_|\_\_|_| |_|\__, |
  #//                                                         |___/ 

  isOver = false
  isHover = false
  isInvalid = false

  
  #//                                       _        _            
  #//  _ __ ___   ___  _   _ ___  ___   ___| |_ __ _| |_ ___  ___ 
  #// | '_ ` _ \ / _ \| | | / __|/ _ \ / __| __/ _` | __/ _ \/ __|
  #// | | | | | | (_) | |_| \__ \  __/ \__ \ || (_| | ||  __/\__ \
  #// |_| |_| |_|\___/ \__,_|___/\___| |___/\__\__,_|\__\___||___/
  #//                                                             

  over = (e)->
    # for invalid state, over is much more reliable than enter
    e.preventDefault()
    isOver = true
    isInvalid = item.ancestry.includes(currentlyDragging._id) 


  leave = (e)->
    isOver = false
    isInvalid = false

  #//            _     _ 
  #//   __ _  __| | __| |
  #//  / _` |/ _` |/ _` |
  #// | (_| | (_| | (_| |
  #//  \__,_|\__,_|\__,_|
  #//                    

  add = (name)->
    console.log "add: index: #{item.index}"
    toIndex = calculatedIndex

    isHover = false

    #console.error "divider add"
    #console.debug { ancestry: item.ancestry, index: toIndex }
    if name?
      dispatch "add", { ancestry: item.ancestry, index: toIndex, name: name }
    else
      dispatch "add", { ancestry: item.ancestry, index: toIndex }






  #//      _                 
  #//   __| |_ __ ___  _ __  
  #//  / _` | '__/ _ \| '_ \ 
  #// | (_| | | | (_) | |_) |
  #//  \__,_|_|  \___/| .__/ 
  #//                 |_|    

  drop = (e)->

    e.preventDefault()

    dropped = !isInvalid
    item.index = calculatedIndex

    if dropped
      dispatch "drop",
        dragItem: currentlyDragging
        dropItem: item 

    else
      console.error "CAN'T DROP ON ANCESTOR OR SELF"

    isOver = false
    isInvalid = false
    
  


  #//                             ____       _           _   
  #//  _ __ ___   ___ _ __  _   _/ ___|  ___| | ___  ___| |_ 
  #// | '_ ` _ \ / _ \ '_ \| | | \___ \ / _ \ |/ _ \/ __| __|
  #// | | | | | |  __/ | | | |_| |___) |  __/ |  __/ (__| |_ 
  #// |_| |_| |_|\___|_| |_|\__,_|____/ \___|_|\___|\___|\__|
  #//                                                        

  menuSelect = (e)->
    switch e.detail
      when "add"
        add()
      when "add-clipboard"
        console.log "added from clipboards"

        parsedLines = []
        navigator.clipboard.readText().then (o)->
          
          dispatch "importClipboard", { _id: item._id, index: item.index, ancestry: item.ancestry, list_items: o }   
        
        #dispatch "importClipboard", item
        ###
          # need to find out previous and next item
          console.error "verify list reference"
          console.log item.list

          parsedLines.forEach (line)->
            console.log "add #{line}"


          # add o
        ###

  debugNote = ""

  determineIndexFor = (idx, list)->
    #console.error "Determine Index for"
    #console.log item
    if item?
      if item.index?
        if idx?
          if list?

            if idx == (list.length - 1) # last item
              debugNote = "last item"
              return item.index + 1000
            else
              # calculate between this one and the next one
              nextItem = list[idx + 1]
              if item.ancestry.isEqual(nextItem.ancestry) # same parent, not last
                #console.log "A same parent not last"
                #console.log "nextItem.index: #{nextItem.index}"
                # siblings Before next item
                siblings = listItems.filter (o)->
                  unless o.index?
                    console.error "o is missing index"
                    console.log o
                  return o.ancestry.isEqual(item.ancestry) && (o.index < nextItem.index)
                #console.error "SIBLINGS for  #{JSON.stringify(item.ancestry)}"
                #console.log siblings
                if siblings.length > 0
                  lastItem = siblings.max("index")
                  debugNote = "A same parent not last, nexItemIndex:#{nextItem.index}, last item was [#{lastItem.index}] #{lastItem.name}"
                  return [lastItem.index, nextItem.index].median()
                else 
                  debugNote = "A same parent not last, nexItemIndex:#{nextItem.index}, no siblings BEFORE"
                  return [item.index, nextItem.index].median()

              else # different parent
                if item.ancestry.length > nextItem.ancestry.length # next item is a level out, last item
                  # find the first item BEFORE this item with same ancestry

                  #console.log "diff parent"  
                  #console.log item
                  siblings = listItems.filter (o)->
                    return o.ancestry.isEqual(item.ancestry)

                  
                  #console.error "siblings"
                  #console.log siblings
                  if siblings.length == 0 # no siblings
                    debugNote = "different parent, no siblings!, probably an orphan above"
                    return 0

                  else
                    lastItem = siblings.max("index")
                    debugNote = "Last item for parent, last item was [#{lastItem.index}] #{lastItem.name}"
                    return lastItem.index + 1000
                else # next item is first child

                  debugNote = "next first child, last item, nexItemIndex:#{nextItem.index}"
                  return [item.index, nextItem.index].median()



        else # override is in item prop
          debugNote = "override is in item prop"
          return item.index
      else
        debugNote = "first child"
        if idx == (list.length - 1) # last item
          debugNote = "lastItem"
          return 0
        else
          nextItem = list[idx + 1]
          if item.ancestry.isEqual(nextItem.ancestry) # same parent, not last
            debugNote = "First child, has sibling ahead"
            return nextItem.index - 1000
          else # different parent
            debugNote = "First child, different parent? item #{JSON.stringify(item.ancestry)} next #{JSON.stringify(nextItem.ancestry)}"
            return 0
    else
      console.error "NO ITEM? WHY"

      console.log index
      console.log indentation
      console.log listItems
      console.log item
      console.log idx
      console.log list

  `$: calculatedIndex = determineIndexFor(index,listItems)`


</script>

<div
  divider
  data-index={calculatedIndex}
  data-ancestry={JSON.stringify(item.ancestry)}
  data-note={debugNote}  
  class:over={isOver}
  on:drop={drop}
  on:dragover={over}
  on:dragleave={leave}
  

>
  <FlexRow>
    <FlexRowCell flex="initial">
      <div spacer/>
    </FlexRowCell>
    <FlexRowCell flex="initial">
      <div class="indentation" style="width: {indentation * 32}px"/>
    </FlexRowCell>
    <FlexRowCell flex>
      <TippyContextMenu template={Menu} bind:tippyComponent={tippyComponent} bind:tippyObject={tippyObject} on:select={menuSelect}>
        <div class:over={isOver} class="placeholder" class:invalid={isInvalid}/>
      </TippyContextMenu>
    </FlexRowCell>
  </FlexRow>
</div>


<style lang="sass">



  [divider]

    --item-draggable-icon-width: 24px 
    height: 8px

    // match footprint to draggable container
    width: calc(100% + calc(2 * var(--item-draggable-icon-width)))
    max-width: calc(100% + calc(2 * var(--item-draggable-icon-width)))
    // 2 icons grabber, expander
    margin-left: calc(var(--item-draggable-icon-width) * -2)



    border-radius: 3px
    [spacer]  
      width: 48px
      height: 2px
    .indentation
      height: 8px
    .placeholder
      //border-radius: 4px
      background: transparent
      height: 8px
      color: transparent

    &:hover
      [spacer]
        background: transparent
      .indentation
        background: var(--lightest-blue)        
      .placeholder
        background: var(--lighter-blue)//var(--list-row-highlight)
    
    &.over
      .indentation
        background: var(--lightest-blue)
      .placeholder
        background: var(--light-blue)//var(--list-row-highlight)
    
        &.invalid
          background: var(--light-gray) !important



</style>
